import { useCallback, useEffect, useRef } from "react";
import { hovering } from "./hovering.js";

export const useFocus = ({
    href,
    onFocus, onBlur,
    onMouseEnter, onMouseLeave
}) => {
    const focus = useRef(false);
    const hover = useRef(false);

    const cb = useCallback(() => {
        if (!focus.current || !hover.current) {
            return;
        }
        hovering(href);
    }, [href]);

    const onFocusWrap = useCallback(e => {
        focus.current = true;
        cb();
        onFocus?.(e);
    }, [onFocus, cb]);
    const onBlurWrap = useCallback(e => {
        focus.current = false;
        onBlur?.(e);
    }, [onBlur]);
    const onMouseEnterWrap = useCallback(e => {
        hover.current = true;
        cb();
        onMouseEnter?.(e);
    }, [onMouseEnter, cb]);
    const onMouseLeaveWrap = useCallback(e => {
        hover.current = false;
        onMouseLeave?.(e);
    }, [onMouseLeave]);

    return {
        onFocus: onFocusWrap, onBlur: onBlurWrap,
        onMouseEnter: onMouseEnterWrap, onMouseLeave: onMouseLeaveWrap
    };
};
